*{
  margin: 0;
  padding: 0;;
  box-sizing: border-box;

}
.container{
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.calc{
  padding: 20px;
  border-radius: 20px;
  background: red;
  border: none;
  box-shadow: -1px -1px 10px 10px brown;
}
#input{
  width: 100%;
  height: 100px;
  max-width: 400px;
  border-radius: 20px;
  padding: 10px;
  border: none;
  background: black;
  text-align: center;
  color: white;
  cursor: pointer;
}

button{
  border: none;
  margin: 10px;
  color: white;
  height: 80px;
  width: 80px;
  border-radius: 20px;
  background: transparent;
  box-shadow: 6px 6px 10px brown,
  -6px -6px 10px brown;
  font-size: 25px;
  cursor: pointer;
}